import { Link } from 'react-router-dom';
import Mascot from '../components/Mascot';
import '../styles/Start.css';

function Start() {
    return (
        <div className="page">
            <div className="start-cont">
                <Mascot/>
                <h2>Welcome!</h2>
                <Link className="el-btn" to="/questions">Start Game</Link>
            </div>
        </div>
    );
}
export default Start;