import { useEffect, useRef, useState } from 'react';
import { Link } from 'react-router-dom';
import Speech from 'react-speech';
import Mascot from '../components/Mascot';
import { getRandomColor } from '../helper/functionality';
import { QuestionSource } from '../services/Questions';
import '../styles/Questions.css';

function Questions() {
    const onEmotionChange = useRef();
    const getRandomQuestion = () => {
        const qList = []; const count = 4;
        const types = ['words', 'sentences'];
        const type = types[Math.floor(Math.random()*types.length)];
        let q = QuestionSource[type][Math.floor(Math.random()*QuestionSource[type].length)];
        for (let i = 0; i < count; i++) {
            while (qList.includes(q)) {
                q = QuestionSource[type][Math.floor(Math.random()*QuestionSource[type].length)];
            }
            qList.push(q);
        }
        return { qList, q: Math.floor(Math.random()*count) };
    }

    const [question, setQuestion] = useState(getRandomQuestion());
    
    const getButtonStyle = () => {
        const color = getRandomColor();
        return `background-color:${color}33;border-color:${color}`;
        // return {backgroundColor: color+'33', borderColor: color};
    }
    let isClicked = true;
    const handleClick = (e) => {
        const value = e === question.q;
        onEmotionChange.current(value);
        if (value) {
            setTimeout(() => { onEmotionChange.current(); }, 700);
            if (isClicked) {
                isClicked = false;
                setTimeout(() => { setQuestion(getRandomQuestion()); }, 1000);
            }
        }
    }
    useEffect(() => {
        const rsPlay = document.getElementsByClassName('rs-play');
        for (let i = 0; i < rsPlay.length; i++) {
            const style = getButtonStyle();
            const element = rsPlay[i];
            const handleLeave = () => { element.style = style; };
            element.style = style;
            element.value = i;
            element.addEventListener('mouseleave', handleLeave);
        }
    }, [question]);
    return (
        <div className="page">
            <div className="questions-cont">
                <div className="question-detail">
                    <div>
                        <div>
                            <Link to="/" className="el-btn">Back</Link>
                            <h2>{question.qList[question.q].tr}</h2>
                            <p><span className="question-type">{question.qList[question.q].type}</span> Question: Choose correct {question.qList[question.q].type}.</p>
                        </div>
                        <div>
                            {question.qList[question.q].img && <img src={question.qList[question.q].img} alt={question.qList[question.q].tr}/>}
                        </div>
                    </div>
                    <div>
                        {question.qList.map((item, itemI) => {
                            return (
                                <div key={itemI}>
                                    <div className="op-btn" onClick={()=>{handleClick(itemI)}}>
                                        <Speech textAsButton displayText={item.en} voice="Google UK English Female" text={item.en}/>
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                </div>
                <div>
                    <Mascot onChange={onEmotionChange}/>
                </div>
            </div>
        </div>
    )
}
export default Questions;